/******************************** 
    slick arrow style
 ********************************/

.slick-arrow {
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  border: 0px;
  background: $color-dark;
  color: $color-white;
  text-align: center;
  z-index: 1;
  font-size: 14px;
  line-height: 50px;
  @include transform(translateY(-50%));
  @include border-radius(50%);
}

.main-slider {
  .slick-prev {
    left: 40px;
    background: $color-dark;
    color: $color-white;
    opacity: 0;
    visibility: hidden;
  }

  .slick-next {
    right: 40px;
    background: $color-dark;
    color: $color-white;
    opacity: 0;
    visibility: hidden;
  }

  &:hover {
    .slick-arrow {
      opacity: 1;
      visibility: visible;
    }
  }
}

// slick-nav
.slick-nav {
  .slick-prev {
    left: -25px;
    @include max-screen(1199){
      left: 0px;
    }
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-prev {
      opacity: 1;
      visibility: visible;
    }
  }
  .slick-next {
    right: -25px;
    @include max-screen(1199){
      right: 0px;
    }
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-next {
      visibility: visible;
      opacity: 1;
    }
  }
}
// slick-nav-sync
.slick-nav-sync {
  .slick-prev {
    left: -15px;
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-prev {
      left: 15px;
      opacity: 1;
      visibility: visible;
    }
  }
  .slick-next {
    right: -15px;
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-next {
      right: 15px;

      visibility: visible;
      opacity: 1;
    }
  }
}
// slick-nav-brand
.slick-nav-brand {
  .slick-prev {
    left: -40px;
    @include max-screen(1024) {
      left: -15px;
    }
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-prev {
      opacity: 1;
      visibility: visible;
    }
  }
  .slick-next {
    right: -40px;
    @include max-screen(1024) {
      right: -15px;
    }
    opacity: 0;
    visibility: hidden;
  }
  &:hover {
    .slick-next {
      visibility: visible;
      opacity: 1;
    }
  }
}
