// theme1
.theme1 .slick-arrow {
  &:hover {
    color: $color-white;
    background: $theme-color;
    border-color: $theme-color;
  }
}


/* slick dots style */
.dots-style {
  .slick-dots {
    position: absolute;
    left: 0;
    bottom: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      line-height: 1;
      button {
        width: 14px;
        height: 14px;
        text-indent: 2000px;
        font-size: 0px;
        display: block;
        margin: 0 5px;
        @include transition(0.3s);
        @include border-radius(50%);
        &:hover {
          @include transform(scale3d(1, 1, 1));
        }
      }
    }
  }
}
// popular-slider

.popular-slider-init {
  &.dots-style {
    .slick-dots {
      bottom: 0px;
      position: static;
      margin-top: 30px;
      li {
        button {
          width: 8px;
          height: 8px;
          @include transition(0.3s linear);
        }
      }
    }
  }
}

// theme1
.theme1 {
  .slick-dots {
    li {
      button {
        background: $color-dark;
        &:hover {
          background: $theme-color;
        }
      }
      &.slick-active {
        button {
          background: $theme-color;
        }
      }
    }
  }
}


// popular-slider
// theme1

.theme1 {
  .popular-slider-init {
    &.dots-style {
      .slick-dots {
        li {
          button {
            background: $color-dark;
            &:hover {
              background: $theme-color;
            }
          }
          &.slick-active {
            button {
              width: 28px;
              background: $theme-color;
              @include border-radius(8px);
            }
          }
        }
      }
    }
  }
}



