body {
  background: #ffffff;
  font-family: "Poppins";
  font-weight: 400;
  font-size: 14px;
  line-height: 1.75;
  color: #565656;
  overflow-x: hidden;
}

@media screen and (max-width: 1199px) {
  .container {
    max-width: 100%;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway";
  margin: 0;
  line-height: 1.25;
  color: #111111;
}

p {
  margin: 0px;
  line-height: 1.715;
}

a,
button {
  text-decoration: none;
  color: inherit;
}

button {
  background: transparent;
  border: 0;
  padding: 0;
  outline: 0;
}

a,
button,
img,
input {
  transition: all 0.3s;
}

a:focus {
  text-decoration: none;
  outline: 0;
}

a:hover {
  text-decoration: none;
  color: #5a5ac9;
}

button,
input[type="submit"] {
  cursor: pointer;
  outline: 0 !important;
}

img {
  max-width: 100%;
}

:focus {
  outline: 0;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

span {
  display: inline-block;
}

span.separator {
  color: #bfbfbf;
  margin: 0 8px;
  line-height: 1;
  font-size: 14px;
}

.zoom-in img {
  transition: all 0.3s linear;
}

.zoom-in:hover img {
  transform: scale(1.045) rotate(0.05deg);
}

.theme-border {
  border: 2px solid #5a5ac9 !important;
}

.theme-border2 {
  border: 2px solid #8a72e0 !important;
}

hr.hr {
  background-color: #ebebeb;
  opacity: 1;
}

span.required {
  color: #5a5ac9;
}

.theme1 .hover-color:hover {
  color: #5a5ac9 !important;
}

.w-md-50 {
  width: 100%;
  max-width: 100%;
}

@media screen and (min-width: 768px) {
  .w-md-50 {
    max-width: 50%;
  }
}

