/* custom button styles */
.search-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 65px;
  height: 100%;
  color: #ffffff;
  font-size: 21px;
  text-align: center;
  transition: all 0.3s;
}

.search-btn.position-left {
